@import "../_core/nguyen";
//Header
.header {
    @include home-block($tim,$trang,50px);
    a.nav-link.nav-tinh {
        color: white;
        margin: .5rem .75rem;
        padding: 5px 15px;
        &:hover {
            background-color: darken($tim,15%);
            border-radius: 3px;
            color: white; } } }
// End-header
// Slider ở trang chủ
.home-slider {
    @include home-block($tim,$trang, 600px);
    a {
        @include button($maunut,$trang); } }
// Các dịch vụ
.home-services {
    @include home-block($ngoc,$trang , 368px);
    .col.mt-5 {
        p {
            font-size: 16pt; } } }
.home-team {
    @include home-block($xemtrang,$mauh2,600px);
    img {
        width: 225px;
        height: 220px;
        border-radius: 50%; }
    li {
        @include icon-circle(); } }
.home-skill {
    @include home-block(null,$mauh2,600px);
    .skill_item {
        .pink {
            .bar {
                border-color: #d74680; }
            .fill {
                border-color: #d74680; } }
        .cyan {
            .bar {
                border-color: #15c7a8; }
            .fill {
                border-color: #15c7a8; } } } }
.home-porft {
    @include home-block($vang,$mauh2,600px);
    li {
        @include button-list();
        a {
            color: $mauh2; } } }
.home-about {
    @include home-block($do,$trang,600px);
    .row.mt-5 {
        p {
            color: darken($vang,10%); } } }
.home-contact {
    @include home-block($xanh,$trang,600px);
    input,textarea {
        background: darken($xanh,10%);
        outline: none;
        border: none;
        &::placeholder {
            color: white;
            margin: .5rem; }
        &:focus {
            background: darken($xanh,10%); } }
    a {
        @include button($ngoc,$trang); } }
.footer {
    @include home-block(darken($xanh,5%),$trang,50px);
    li {
        @include list-item();
        a {
            color: white;
            &:hover {
                color: $ngoc; } } } }
@media (min-width: 576px) {
    .home-about {
        .tinh-left {
            text-align: left!important; } } }

